.AdmHeaderPage {
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;

  @include respond-to($mbl) {
    flex-direction: column;
    align-items: flex-start;
  }

  &__title {
    h1 {
      @include font-size(34, 44);
      font-weight: 600;
    }
  }
}
